.main {
  height: 70vh;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 50%;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: hidden;
  @include respond(largeTablet) {
    height: 70vh;
    width: 60%;
  }
  @include respond(tablet) {
    height: 75vh;
    width: 80%;
  }
  @include respond(largePhone) {
    height: 75vh;
    width: 90%;
  }
}