*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-weight:400;font-family:"Quicksand",sans-serif;font-size:16px;line-height:1.7;color:#616161;padding-bottom:2rem;background-color:#90CAF9;background-image:url(/weather-app/static/media/weather-background.9c07387c.gif)}a:link,a:visited{color:#1976D2;text-decoration:none}a:hover,a:active{color:#90CAF9}button{background-color:#1976D2;border:none}span{display:inline !important}.wrapper{margin-right:auto;margin-left:auto;height:100%;max-width:960px;padding-right:10px;padding-left:10px;display:flex;align-items:center;justify-content:center}.main{height:70vh;box-shadow:0px 13px 40px -13px rgba(0,0,0,0.75);width:50%;margin:0 auto;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-bottom-left-radius:20px;border-bottom-right-radius:20px;overflow:hidden}@media (max-width: 1024px){.main{height:70vh;width:60%}}@media (max-width: 768px){.main{height:75vh;width:80%}}@media (max-width: 600px){.main{height:75vh;width:90%}}.header{text-align:center;border:transparent;background-color:#1976D2;padding:2rem 3rem;border-top-left-radius:20px;border-top-right-radius:20px}.header__title{font-weight:600;color:white}.content{background-color:white;width:100%;height:100%;padding:1rem 3rem}.date-time{margin-top:1rem;text-align:center;color:#1976D2}.tagline{font-size:2rem;width:100%;text-align:center;margin-top:2rem;letter-spacing:0.7px}@media (max-width: 600px){.tagline{font-size:1.5rem}}.weather-search__form{text-align:center;margin-top:3rem;margin-bottom:5rem}.weather-search__input{display:inline-block;border:solid 2px transparent;background-color:rgba(25,118,210,0.1);padding:1rem 3rem;border-top-left-radius:10px;border-bottom-left-radius:10px;margin-right:2rem;padding-left:1rem;padding-right:4rem}.weather-search__input:focus{outline:none;border-bottom:solid 2px #1976D2}.weather-search__submit{display:inline-block;text-align:center;background-color:transparent;font-size:3rem;height:30px;width:30px;position:relative;top:0.9rem;margin-left:-2rem}.weather-search__button{padding:1.2rem;color:white;position:absolute;top:50%;left:50%;border-top-right-radius:10px;border-bottom-right-radius:10px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.weather-search__button:focus{outline:none}.weather-data{text-align:center;margin-top:3rem}.weather-data__city{color:#E53935}.weather-data__box{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"current-temp high-low" "humidity pressure"}.weather-data__box .current-temp{grid-area:current-temp}.weather-data__box .high-low{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:0.5fr 2fr 0.5fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:". ." "high low" ". .";grid-area:high-low}.weather-data__box .high{grid-area:high}.weather-data__box .low{grid-area:low}.weather-data__box .humidity{grid-area:humidity}.weather-data__box .pressure{grid-area:pressure}@media (max-width: 768px){.weather-data__box{margin-top:2rem}}@media (max-width: 600px){.weather-data__box{margin-top:4rem}}.weather-data__property{margin:0 3rem}@media (max-width: 400px){.weather-data__property{margin:0 2rem}}.weather-data__title{font-size:1rem;text-transform:uppercase;font-weight:600;letter-spacing:1px;margin:0 0 1rem 0}.weather-data__value{color:#1976D2;font-size:4rem;line-height:0.6;margin:0 0 1.5rem 0}@media (max-width: 400px){.weather-data__value{font-size:3rem}}.weather-data__title2{font-size:1rem;text-transform:uppercase;font-weight:600;letter-spacing:1px}.weather-data__value2{color:#1976D2;font-size:3rem;line-height:0.6;margin:0 0 1.5rem 0}@media (max-width: 400px){.weather-data__value2{font-size:3rem}}.weather-data__icons{width:30px;margin:1rem 0 0 0}.error{margin-top:3rem;text-align:center}.footer{position:absolute;bottom:0;padding:1.5rem 0;left:50%;text-align:center;-webkit-transform:translateX(-50%);transform:translateX(-50%);border-top:solid 2px rgba(144,202,249,0.3);letter-spacing:0.3px}@media (max-width: 600px){.footer{letter-spacing:0;width:90%}}

