.weather-data {
  text-align: center;
  margin-top: 3rem;

  &__city {
    color: $colour-3;
  }

  &__box {
    /*display: flex;
    align-items: center;*/

    /*justify-content: center;
    margin-top: 5rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 5px 0px;
    grid-template-areas:
    "current-temp current-temp humidity pressure"
    "high low . .";*/

    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
     "current-temp high-low"
      "humidity pressure";

    .current-temp { grid-area: current-temp; }
    .high-low {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 0.5fr 2fr 0.5fr;
      gap: 0px 0px;
      grid-template-areas:
        ". ."
        "high low"
        ". .";
      grid-area: high-low;
    }
    .high { grid-area: high; }
    .low { grid-area: low; }
    .humidity { grid-area: humidity; }
    .pressure { grid-area: pressure; }

    @include respond(tablet) {
      margin-top: 2rem;
    }
    @include respond(largePhone) {
      margin-top: 4rem;
    }
  }

  &__property {
    margin: 0 3rem;
    @include respond(smallPhone) {
      margin: 0 2rem;
    }
  }

  &__title {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1px;
    margin: 0 0 1rem 0;
  }

  &__value {
    color: $colour-2;
    font-size: 4rem;
    line-height: 0.6;
    margin: 0 0 1.5rem 0;
    @include respond(smallPhone) {
      font-size: 3rem;
    }
  }
// Min/Max Temp
  &__title2 {
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1px;
  }

  &__value2 {
    color: $colour-2;
    font-size: 3rem;
    line-height: 0.6;
    margin: 0 0 1.5rem 0;
    @include respond(smallPhone) {
      font-size: 3rem;
    }
  }

  &__icons {
    width: 30px;
    margin: 1rem 0 0 0;
  }
}